
.dialog {
    height: 100%;
    display: flex;
    flex-direction: column;
    border-left: 1px solid #e1e1e1;
    border-right: 1px solid #e1e1e1;
}

.dialog_header {
    border-bottom: 1px solid #e1e1e1;
    box-sizing: border-box;
    padding: 0.5em;
    font-size: 1.5em;
    position: sticky;
    top: 0;
}

.messages {
    flex-grow: 1;
    box-sizing: border-box;
}



.message {
    width: 80%;
    border: 1px solid #e1e1e1;
    padding: 0 1em 1em 1em;
    margin: 5px;
    border-radius: 1.5em 1.5em 1.5em 0;
    box-sizing: border-box;
}

.message.outer {
    background-color: #eaedef;
}

.message.inner {
    background-color: #fafdff;
    margin-left: calc(20% - 5px);
    border-radius: 1.5em 1.5em 0 1.5em ;
}

.message_header {
    display: flex;
    flex-direction: row;
    padding: 5px 0;
    box-sizing: border-box;
}

.message_header .name_sender {
    flex-grow: 1;
    font-weight: bold;
}

.message_header .time_sender {
    font-size: 0.8em;
    color: #777;
}


.Dialogs_wrapper {
    display: grid;
    width: 100%;
    grid-template-columns: 3fr 7fr;
    height: 100%;
}

.Dialogs_wrapper .dialog {
    flex-grow: 1;
}
